input{
    outline:none;
}

#search-bar{
    border-radius: 50px;
}
.wrapper {
    position: relative;
    display: flex;
    min-width: 100px;
}

.search-icon {
    position: absolute;
    top: 8px;
    left: 8px;
    width: 14px;
  }



.search {
    border: 1px solid grey;
    border-radius: 5px;
    height: 30px;
    width: 100%;
    padding: 2px 23px 2px 30px;
    outline: 0;
    background-color: #f5f5f5;
  }
  .search:hover, .search:focus {
    border: 1.5px solid #009688;
    background-color: white;
  }


  #persons {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  #persons td, #persons th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #persons tr:nth-child(even){background-color: #f2f2f2;}
  
  #persons tr:hover {background-color: #ddd;}
  
  #persons th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #45a29e;
    color: white;
  }

  footer{
    background-color:#0B0C10;
    text-align:right;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
  }

.primary-btn, .delete-btn{
  border-radius:20px 20px 20px 20px;
  margin:8px;
  padding:10px;
  width:100px;
  outline:none;
  color:white;
  border:none;

}
.primary-btn{
  background-color:#24a0ed;
}

.delete-btn{
  background-color:#FF0000;
  
}
#person-form-navbar{
    background-color:#0b0c10;
    display:flex;
    width:31.25rem;
    height:2.35rem;
    align-items:center;
  
}
#person-form-header{

  color:#45a29e;
  font-weight:bold;
}
.person-form-item{
  margin:4px;
  display:flex;
  justify-content:space-between;
  width:28rem;

}
.person-form>input{
  padding:4px;
  margin:6px;

}
.overlay{
  background-color:white;
  position: fixed;
  width: 500px;
  height: 200px;
  border: 0.5px solid;
  top: 50%;
  left: 50%;
  margin-top: -100px;  /* Negative half of height. */
  margin-left: -250px;  /* Negative half of width. */
}
.modal-container{
  align-items:center;
}
.showAdd{
  opacity:1
}
.freezeAdd{
  opacity:0.5
}
.close-button{
  position:absolute;
  right:0;
  top: 0;
  padding: 1px 8px;
  background-color: red;
  outline: none;
  border: none;
  font-size: 25px;
  color:white;
  cursor:pointer;
  
  
}

.save-btn{
  align-items:center;
  
}